import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import { gsap } from "gsap";
import $ from 'jquery'

export const Header = () => {

    const [hamburger, setHamburgerActive] = useState(false);
    const Menu = () => {
        setHamburgerActive(true)
    };
    const hamburguercloseanimation = useRef();
    const Close = () => {
        setHamburgerActive(false);
    };

    const menuanimation = useRef();
    const menuanimationup = gsap.utils.selector(menuanimation);
    useEffect(() => {
        gsap.from(menuanimationup('.up'), {
            y: "-60",
            stagger: 1,
            repeat: 0
        });

        gsap.to(menuanimationup('.up'), {
            y: '0',
            stagger: 1,
            repeat: 0
        });
    });
    useEffect(() => {
        gsap.from(menuanimationup('.down'), {
            y: "60",
            stagger: 1,
            repeat: 0,
            delay: 0.5
        });

        gsap.to(menuanimationup('.down'), {
            y: '0',
            stagger: 1,
            repeat: 0,
            delay: 0.5
        });
    },);

    const ScrollToAboutMe = e => {
        e.preventDefault();

        $('html, body').animate({
            scrollTop: $('#aboutme').offset().top
       }, 1);
    }

    const ScrollToProyects = e => {
        e.preventDefault();

        $('html, body').animate({
            scrollTop: $('#proyects').offset().top
       }, 1);
    }

    const ScrollToTechnologies = e => {
        e.preventDefault();

        $('html, body').animate({
            scrollTop: $('#technologies').offset().top
       }, 1);
    }

    const ScrollToLetsTalk = e => {
        e.preventDefault();

        $('html, body').animate({
            scrollTop: $('#letstalk').offset().top
       }, 1);
    }

    return (
        <header ref={menuanimation} id='header'>
            <div>
                <h1 className='up'><Link to='/home'>Edwin Daniel Landeros Sierra</Link></h1>
            </div>
            <nav>
                <ul id='menu'>
                    <li className='down' ><a onClick={ScrollToAboutMe} href='/home'>About me</a></li>
                    <li className='up' ><a onClick={ScrollToProyects} href='/home'>Proyects</a></li>
                    <li className='down'><a onClick={ScrollToTechnologies}  href='/home'>Technologies</a></li>
                    <li className='up' ><a  onClick={ScrollToLetsTalk}  href='/home'>Let's talk!!</a></li>
                </ul>
                <a onClick={Menu} id='hamburgerlogo' href="/#"><i className="fas fa-bars"></i></a>
            </nav>
            <div ref={hamburguercloseanimation} className={hamburger ? 'hamburger-menu active' : 'hamburger-menu'} >
                <ul>
                    <li><a onClick={ScrollToAboutMe} href="/home">About me</a></li>
                    <li><a onClick={ScrollToProyects} href="/home">Proyects</a></li>
                    <li><a onClick={ScrollToTechnologies} href="/home">Technologies</a></li>
                    <li><a onClick={ScrollToLetsTalk}  href="/home">Let's talk!!</a></li>
                </ul>
                <a onClick={Close} href="/#" className="close">
                    <i className="fas fa-times"></i>
                </a>
            </div>
        </header>
    );
}