import React from 'react';
import CvEdwinSierra from '../assets/pdf/CV-EdwinSierra.pdf';

import PersonAndBoxes from '../assets/images/boxesperson.svg';
import SvgHeart from '../assets/images/heart.svg';

export const Home = () => {
    return (
        <section id='home'>
            <div id='inhome'>
                <h1 id='bigh1'>Front - end Developer</h1>
                <p>
                    Development of web sites, applications and I am personally quite interested in web design, Welcome to my PORTAFOLIO!!
                </p>
                <div id='cv'>
                    <a href={CvEdwinSierra} download="CV-EdwinLanderos">
                        <button id='buttoninhome'><img src={SvgHeart} alt='start' width="30" />Download CV!!</button>
                    </a>
                </div>
            </div>
            <div id='svgbox1'>
                <img src={PersonAndBoxes} alt='svgbox' />
            </div>
        </section>
    )
}


