import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import emailjs from '@emailjs/browser';

import SvgInstagram from '../assets/images/technologies/instagram.svg';
import SvgLinkedin from '../assets/images/technologies/linkedin.svg';
import SvgGitHub from '../assets/images/technologies/github.svg';
import SvgFacebook from '../assets/images/technologies/facebook.svg'

export const Letstalkclick = () => {
    const letstalkclick = useRef();
    const letstalkclickanimation = gsap.utils.selector(letstalkclick);

    useEffect(() => {
        gsap.from(letstalkclickanimation('.imgleft'), {
            x: '-150',
            repeat: 0,
            opacity: 0
        });

        gsap.to(letstalkclickanimation('.imgleft'), {
            x: '-20',
            repeat: 0,
            opacity: 1
        });

        gsap.from(letstalkclickanimation('.imgdown'), {
            y: '25',
            repeat: 0,
            opacity: 0,
            stagger: 0.4,
            delay: 0.4
        });

        gsap.to(letstalkclickanimation('.imgdown'), {
            y: '0',
            repeat: 0,
            opacity: 1,
            stagger: 0.4,
            delay: 0.4

        });

        gsap.from(letstalkclickanimation('.imgright'), {
            x: '80',
            repeat: 0,
            opacity: 0,
            delay: 1.2
        });

        gsap.to(letstalkclickanimation('.imgright'), {
            x: '-20',
            repeat: 0,
            opacity: 1,
            delay: 1.2

        });
    });

    const form = useRef();
    const names = useRef();
    const surnames = useRef();
    const email = useRef();
    const plus = useRef();

    const getdata = e => {
        e.preventDefault();

        if (names.current.value.trim() == null || names.current.value.trim().length === 0 || Number(names.current.value.trim())) {
            alert('Name just receives words');
            return false;
        }

        if (surnames.current.value.trim() == null || surnames.current.value.trim().length === 0 || Number(surnames.current.value.trim())) {
            alert('Surname just receives words');
            return false;
        }

        if (email.current.value.trim() == null || email.current.value.trim().length === 0 || Number(email.current.value.trim())) {
            alert('Email just receives words');

            return false;
        }

        if (plus.current.value.trim() == null || plus.current.value.trim().length === 0 || isNaN(plus.current.value.trim())) {
            alert('The just receives numbers!');
            return false;
        }

        if (parseFloat(plus.current.value.trim()) !== 10) {
            alert('The addition is wrong! :>');
            return false;
        }
        
        emailjs.sendForm('service_3jjpjkq', 'template_bc2qmbh', form.current, 'HmKEtP1SJQJ-0rYJu')
        .then((result) => {
            alert(result.text = 'The form has been successfully sent!^^');
        }, (error) => {
            alert(error.text = 'Something has not worked well(');
        });

        e.target.reset();
    };

    return (
        <>
            <div id='letstalkclick'>
                <div ref={letstalkclick} id='socialmedia'>
                    <h3 id='socialmediatitle'>Social media!!^^</h3>
                    <div id='insocialmedia' >
                        <a className='linksocialmedia' href="https://www.linkedin.com/in/edwin-daniel-landeros-sierra-5498a6218/" ><img className='imgleft' src={SvgLinkedin} width="45" alt="linkedin" /></a>
                        <a className='linksocialmedia' href="https://www.instagram.com/edwin_sierra_web/" ><img className='imgdown' src={SvgInstagram} width="45" alt="instagram" /></a>
                        <a className='linksocialmedia' href="https://www.facebook.com/edwindaniel.landerossierra.1" ><img className='imgdown' src={SvgFacebook} width="45" alt="facebook" /></a>
                        <a className='linksocialmedia' href="https://github.com/EdwinSierraWeb" ><img className='imgright' src={SvgGitHub} width="45" alt="github" /></a>
                    </div>
                </div>
                <div id='forms'>
                    <form onSubmit={getdata} ref={form} >
                        <label htmlFor="name">Name(s): </label>
                        <input ref={names} type="text" name='form_names' placeholder='Names...' />

                        <label htmlFor="surnames">Surname(s): </label>
                        <input ref={surnames} type="text" name='form_surnames' placeholder='Surnames...' />

                        <label htmlFor="email">Email: </label>
                        <input ref={email} type="email" name='form_email' placeholder="Email..." />

                        <label htmlFor="plus">5 + 5: </label>
                        <input ref={plus} type="number" name='form_plus' placeholder='Result...' />

                        <input type="submit" value="Send form" />
                    </form>
                </div>

            </div>
            <div id='letstalkfooter'>
                My social media &copy;
            </div>
        </>

    );
}
