import React from 'react';

import SvgHtml from '../assets/images/technologies/html5.svg';
import SvgCss from '../assets/images/technologies/css3.svg';
import SvgBoostrap from '../assets/images/technologies/bootstrap.svg';
import svgjavascript from '../assets/images/technologies/js.svg';

export const Proyect4 = () => {
  return (
    <div className='allproyects'>
      <h3 className='allproyectstitle'>Calculator!</h3>
      <section className='website-reppository'>
        <div className='in-web-rep'>
          <a href='https://edwinsierraweb.github.io/proyect4/'>Website</a>
        </div>
        <div className='in-web-rep'>
          <a href='https://github.com/EdwinSierraWeb/proyect4'>Repository</a>
        </div>

      </section>
      <section className='allproyectsaboutproyect'>
        <div className='inallproyectsaboutproyect'>
          <h4>
          Why did I make this proyect? 
          </h4>
          <p>
            I made it bcz I wanted to practice using JavaScript and my programming logic. It was funny to make it!^^
          </p>
        </div>
        <div className='inallproyectsaboutproyect'>
          <h4>
            Technologies I used
          </h4>
          <div>
            <img src={SvgHtml} width='65' alt='svghtml' />
            <img src={SvgCss} width='65' alt='svgcss3' />
            <img src={svgjavascript} width='65' alt='svgcss3' />
          </div>
        </div>
      </section>

      <h3 className='allproyectstitle'>First Portafolio!</h3>
      <section className='website-reppository'>
        <div className='in-web-rep'>
          <a href='https://edwinsierraweb.github.io/proyect3-minified/'>Website</a>
        </div>
        <div className='in-web-rep'>
          <a href='https://github.com/EdwinSierraWeb/proyect3'>Repository</a>
        </div>
      </section>
      <section className='allproyectsaboutproyect'>
        <div className='inallproyectsaboutproyect'>
          <h4>
          Why did I make this proyect? 
          </h4>
          <p>
          I made it bcz I wanted to practice using the api called 'Interaction Observer' and practice bootstrap at the same time!^^
          </p>
        </div>
        <div className='inallproyectsaboutproyect'>
          <h4>
            Technologies I used
          </h4>
          <div>
            <img src={SvgHtml} width='65' alt='svghtml' />
            <img src={SvgCss} width='65' alt='svgcss3' />
            <img src={SvgBoostrap} width='65' alt='svgcss3' />
            <img src={svgjavascript} width='65' alt='svgcss3' />
          </div>
        </div>
      </section>
    </div>
  )
}
