import React from 'react';

import SvgTest from '../assets/images/test.svg';
import SvgHtml from '../assets/images/technologies/html5.svg';
import SvgCss from '../assets/images/technologies/css3.svg';
import SvgAngular from '../assets/images/technologies/angular.svg';
import SvgBoostrap from '../assets/images/technologies/bootstrap.svg';
import SvgGitHub from '../assets/images/technologies/github.svg';
import SvgFigma from '../assets/images/technologies/figma.svg';
import SvgJavascript from '../assets/images/technologies/js.svg';
import SvgReact from '../assets/images/technologies/react.svg';
import SvgSass from '../assets/images/technologies/sass.svg';
import SvgTypescript from '../assets/images/technologies/typescript.svg';
import SvgVueJs from '../assets/images/technologies/vue.svg';
import SvgThreeJs from '../assets/images/technologies/svgthreejs.svg';

export const Technologies = () => {
    return (
        <div id='technologies'>
            <article id='masteredtechnologies'>
                <div id='inmasteredtechnologiestitle'>
                    <h3>Mastered Technologies Mastered Technologies Mastered Technologies </h3>
                </div>

                <div id='inmasteredtechnologies'>
                    <div>
                        <img src={SvgHtml} alt='svghtml' />
                    </div>
                    <div>
                        <img src={SvgCss} alt='svgcss3' />
                    </div>
                    <div id='javascript2'>
                        <img src={SvgJavascript} alt='svgjavascript' />
                    </div>
                    <div>
                        <img src={SvgReact} alt='svgreact' />
                    </div>
                    <div>
                        <img src={SvgBoostrap} alt='svgboostrap' />
                    </div>
                    <div>
                        <img src={SvgSass} alt='svgsass' />
                    </div>
                    <div>
                        <img src={SvgGitHub} alt='svggithub' />
                    </div>
                    <div>
                        <img src={SvgFigma} alt='svgfigma' />
                    </div>
                </div>
                <svg viewBox="0 0 1380 1147" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path id='wave2' d="M763.725 966.242C481.725 436.742 217.325 820.241 233.725 472.241C326.414 211.758 834.225 630.242 744.225 174.242C654.225 -281.758 1514.72 435.241 1172.22 472.241C829.725 509.242 1045.72 1495.74 763.725 966.242Z" fill="url(#paint0_linear_0_1)" />
                    <defs>
                        <linearGradient id="paint0_linear_0_1" x1="743.098" y1="23" x2="743.098" y2="1122.09" gradientUnits="userSpaceOnUse">
                            <stop stopColor="#14FFE3" />
                            <stop offset="1" stopColor="#FF87F3" />
                        </linearGradient>
                    </defs>
                </svg>

            </article>
            <article id='technologiesindevelopment'>
                <div id='inmasteredtechnologiestitle2'>
                    <h3>Technologies in development Technologies in development Technologies in development</h3>
                </div>
                <div id='imagesandboxes'>
                    <div className='test' >
                        <img id='svgtest' src={SvgTest} alt='svgtext' />
                    </div>
                    <div id='intechnologiesindevelopment'>
                        <div>
                            <img src={SvgTypescript} alt='svgtypescript' />
                        </div>
                        <div>
                            <img src={SvgVueJs} alt='svgvuejs' />
                        </div>

                        <div>
                            <img src={SvgAngular} alt='svgangular' />
                        </div>
                        <div id='threejs'>
                            <img src={SvgThreeJs} alt='svgthreejs' />
                        </div>
                    </div>
                </div>
            </article>
        </div>
    )
}
