import React, { useState, useRef, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { gsap } from 'gsap';
import { Home } from './Home';
import { AboutMe } from './AboutMe';
import { LetsTalk } from './LetsTalk';
import Proyects from './Proyects';
import { Technologies } from './Technologies';
import { Footer } from './Footer';

export const Body = () => {

    const movebutton = useRef();
    useEffect(() => {
        if(btn === true){
            gsap.from(movebutton.current, {
                y: '0',
                x: '0',
                duration: 0.6,
                repeat: 0
            });

            gsap.to(movebutton.current, {    
                y: "100px",
                x: '460%',
                duration: 0.6,
                repeat: 0
            });
        }  
        
        if(btn === false){
            gsap.from(movebutton.current, {    
                y: "100px",
                x: '460%',
                duration: 0.6,
                repeat: 0
            });
            
            gsap.to(movebutton.current, {
                y: '0',
                x: '0',
                duration: 0.6,
                repeat: 0
            });
        }  
    });

    const [btn, setBtn] = useState(false);
    let receiveData = (data) => {
        setBtn(data);
    };  
    
    return (
        <div >
            <div ref={movebutton} className =  {btn ? 'observerbtn overbtnletstalk' : 'observerbtn '} >
                <Link to="/letstalk" className='btnletstalk'>
                    Let's talk!!
                </Link>
            </div>
            
            <Home />
            <div id='firststicker'></div>
            <div id='twosections'>
                <AboutMe />
                <Proyects />
            </div>
            <div id='secondsticker'></div>
            <div id='anothertwosections'>
                <Technologies />
                <LetsTalk
                getData= {receiveData}
                />
            </div>
            <Footer />
        </div>
    );
    
}
