import React, {Component} from 'react';
import{Link} from 'react-router-dom';

class Proyects extends Component{

    render(){
        return(
            <div id='proyects'>
                <article id='inproyects1'>
                    <Link to="/proyect1" className='proyect'>
                        <div className='divimageproyect1'></div>
                    </Link>
                    <Link to="/proyect2" className='proyect'>
                        <div className='divimageproyect2'></div>
                    </Link>
                    <Link to="/proyect3" className='proyect'>
                        <div className='divimageproyect3'></div>
                    </Link>
                    <Link to="/proyect4" className='proyect'>
                        <div className='divimageproyect4'></div>
                    </Link> 
                </article>
                <article id='inproyects2'>
                    <h3>
                        Proyect
                    </h3>
                    <h3>
                        Best
                    </h3>
                    <h3>
                        Designs
                    </h3>
                    <h3>
                        Lovely
                    </h3>
                </article>
            </div>
        );
    }
}

export default Proyects;