import React from 'react';
import SvgHtml from '../assets/images/technologies/html5.svg';
import SvgCss from '../assets/images/technologies/css3.svg';
import svgjavascript from '../assets/images/technologies/js.svg';
import svgJqueryUi from '../assets/images/technologies/jquery-ui.svg';

export const Proyect3 = () => {
  return (
    <div className='allproyects'>
      <h3 className='allproyectstitle'>Proyect3</h3>
      <section className='website-reppository'>
        <div className='in-web-rep'>
          <a href='https://edwinsierraweb.github.io/proyect5/index.html'>Website</a>
        </div>
        <div className='in-web-rep'>
          <a href='https://github.com/EdwinSierraWeb/proyect5'>Repository</a>
        </div>
      </section>
      <section className='allproyectsaboutproyect'>
        <div className='inallproyectsaboutproyect'>
          <h4>
            Why did I make this proyect?
          </h4>
          <p>
            I made it to practice using JavaScript and JqueryUi, I added a timer, slider, form with validation of course I can change the href of the css text, among other things. 
          </p>
        </div>
        <div className='inallproyectsaboutproyect'>
          <h4>
            Technologies I used
          </h4>
          <div>
            <img src={SvgHtml} width='65' alt='svghtml' />
            <img src={SvgCss} width='65' alt='svgcss3' />
            <img src={svgjavascript} width='65' alt='svgjavascript' />
            <img src={svgJqueryUi} width='65' alt='svgjavascript' />
          </div>
        </div>
      </section>
    </div>
  )
}
