import React from "react";
import { Routes, Route, HashRouter } from 'react-router-dom';
import { Body } from '../components/Body';
import { Header } from '../components/Header';
import { Letstalkclick } from "../components/Letstalkclick";
import { Proyect1 } from "../components/Proyect1";
import { Proyect2 } from "../components/Proyect2";
import { Proyect3 } from "../components/Proyect3";
import { Proyect4 } from "../components/Proyect4";
import { Error } from '../components/Error';

export const AppRouter = () => {

  return (
    <HashRouter>
      <Header />
      <Routes>
        <Route path="/" element={<Body />} />
        <Route path="/home" element={<Body />} />
        <Route path="/letstalk" element={<Letstalkclick />} />
        <Route path="/proyect1" element={<Proyect1 />} />
        <Route path="/proyect2" element={<Proyect2 />} />
        <Route path="/proyect3" element={<Proyect3 />} />
        <Route path="/proyect4" element={<Proyect4 />} />
        
        <Route path="*" element={<Error />} />
      </Routes>
    </HashRouter>
  )
}
