import React from 'react'
import SvgCouple from '../assets/images/couplelove.svg';

export const AboutMe = () => {
    return (
        <div id='aboutme'>
            <article>
                <div id='inaboutme'>
                    <h3>
                        ABOUT ME
                    </h3>
                </div>
                <div id='inaboutme2'>
                    <p>Hello I am Edwin Daniel Landeros Sierra, I'm self-taught front-end developer and not to mention that I am studying Software Engineering.

                        Briefly I love art, living in nature, coding, spending time with my beloved ones, listening to music, drinking coffee and so on and so forth.

                        My plans for future is to live in woods loving life, living at peace every day and of course working for a good company.

                        I would be so glad if we get in touch to work together or whatever you have mind.</p>
                </div>
            </article>
            <div>
                <img id='svgcouple' src={SvgCouple} alt="svgcouple" />
            </div>

        </div>
    )
}
