import React from 'react';
import SvgHtml from '../assets/images/technologies/html5.svg';
import SvgCss from '../assets/images/technologies/css3.svg';
import SvgReact from '../assets/images/technologies/react.svg';

export const Proyect2 = () => {
  return (
    <div className='allproyects'>
      <h3 className='allproyectstitle'>Proyect Films</h3>

      <section className='website-reppository'>
        <div className='in-web-rep'>
          <a href='https://edwinsierraweb.github.io/proyectfilms/'>Website</a>
        </div>
      </section>
      <section className='allproyectsaboutproyect'>
        <div className='inallproyectsaboutproyect'>
          <h4>
            Why did I make this proyect?
          </h4>
          <p>
            This is an app of films. I made it to practice using react of course and realized I know more than I had thought, this is an app of films, bascically more or less a CRUD.
            Check the video below!^^
          </p>
        </div>
        <div className='inallproyectsaboutproyect'>
          <h4>
            Technologies I used
          </h4>
          <div>
            <img src={SvgHtml} width='65' alt='svghtml' />
            <img src={SvgCss} width='65' alt='svgcss3' />
            <img src={SvgReact} width='65' alt='svgcss3' />
          </div>
        </div>
      </section>
      <section id='video-section'>
        <h4>
          Unfortunately I can not add my repository bcz you know "Git-hub". I'll leave a video below with screenshots!^^
        </h4>
        <iframe src="https://www.youtube.com/embed/2hwdshuH05Q" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
      </section>
    </div>
  )
}
